.btn {
    @include mid-flex;
    width: fit-content;
    text-align: center;
    cursor: pointer;
    transition: 0.4s ease;
    overflow: hidden;
    position: relative;
    background: transparent;
    gap: 2rem;
    font-size: 1.6rem;
    height: 4.8rem;
    background-color: white;
    white-space: nowrap;
    font-weight: 600;
    @include font-pri;
    border: 0.1rem solid transparent;
    min-width: 10rem;
    transition: 0.4s;
    border-radius: inherit;

    &.rev {
        .txt {
            flex-direction: row-reverse;
        }
    }

    &-box {
        width: fit-content;
        transition: 0.4s;
        border-radius: 100rem;


        &:has(.full) {
            width: 100%;
        }

        &:has(.center) {
            margin: 0 auto;
            margin-top: 2.4rem;
        }

        &:has(.btn:hover) {
            filter: drop-shadow(0 .2rem .5rem rgba(0, 0, 0, 0.3));
        }
    }

    &.full {
        width: 100%;
    }

    &.center {
        margin: auto;
    }

    >* {
        position: relative;
        // z-index: 2;
    }

    .txt {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        transition: 0.4s;
        gap: 1rem;
        line-height: 1;
        padding: .8rem 2rem;
        width: 100%;

        &-icon {
            width: 1.8rem;
            height: 1.8rem;
            transition: 0.4s;
            flex-shrink: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 100%;
                height: 100%;
            }
        }
    }

    @include hover {
        // filter: brightness(1.1);
    }

    @include max(1200px) {
        height: 3rem;

        .txt {
            font-size: 1.4rem;
            padding: .4rem 1rem;
            padding-left: 1.6rem;
        }
    }

    &.pri {
        background: var(--color-linear);

        .txt {
            color: white;
        }

        img {
            filter: brightness(0) invert(1);
        }

        @include hover {
            background-color: var(--color-second);


            .txt {
                color: white;
            }

            img {
                filter: brightness(0) invert(1);
            }
        }

    }

    &.second {
        background: var(--color-linear2);

        &::after {
            background-color: white;
        }

        .txt {
            color: white;
        }

        @include hover {
            border-color: var(--color-second);
            background-color: white;

            .txt {
                color: var(--color-second);
            }

            img {
                filter: var(--filter-second);
            }
        }
    }

    &.type2 {
        border: none !important;

        .txt {
            padding: .4rem;
            padding-left: 1.6rem;

            &-icon {
                width: 4rem;
                height: 4rem;

                img {
                    filter: none;
                }
            }
        }

        @include hover {
            .txt {
                color: white;

                &-icon {
                    transform: rotate(45deg);
                }
            }
        }

        @include max(1200px) {
            height: 3rem;

            .txt {
                font-size: 1.4rem;
                padding-left: 1.6rem;

                &-icon {
                    width: 2.2rem;
                    height: 2.2rem;
                }
            }
        }
    }



    &:not(:has(img)) {
        .txt {
            padding: .8rem 2rem !important;
        }
    }


}

.btn-detail {
    .txt {
        @include fz-18;
        font-weight: 700;
        color: var(--color-pri);
        transition: 0.4s;

        @include hover {
            color: var(--color-second);
        }
    }
}
.btn-seemore {
    display: flex;
    align-items: center;
    gap: .8rem;
    color: var(--color-second);
    transition: 0.4s;
    .txt {
        @include fz-18;
        font-weight: 700;
    }
    i {
        font-size: 0.7em;
    }
    @include hover {
        color: var(--color-pri);
    }
}