.input {
    @include font-pri;
    width: 100%;

    &-area {
        height: auto;
    }
}


.re {
    &-label {
        color: black;
        font-weight: 700;
        margin-bottom: 0.8rem;
        display: block;
        font-size: 1.4rem;
    }

    &-input {
        padding: 1.4rem;
        width: 100%;
        @include font-pri;
        border: none;
        outline: none;
        display: block;
        border: .1rem solid var(--cl-bd);
        border-radius: .8rem;
        font-size: 1.4rem;
        font-weight: 600;

        &:focus {
            border-color: var(--color-second);
        }

        @include input-placeholder {
            color: var(--color-text);
        }
    }


}

textarea {
    resize: none;
}

form {
    .wpcf7-not-valid-tip {
        color: var(--color-pri);
        bottom: .1rem;
        right: .6rem;
    }

    .btn {
        .wpcf7-spinner {
            position: absolute;
        }
    }

    .wpcf7-form-control-wrap {
        display: block;
        position: relative;
    }
}



.wpcf7-response-output {
    text-align: center;
    // color: var(--color-pri) !important;
}

.ft-regi-btn {
    cursor: pointer;
}


.form {
    --radius: 0.8rem;
    --text: var(--color-text);

    &-list {
        --cg: 1.6rem;
        --rg: 1.6rem;
    }

    &-ip {
        .btn-box {
            width: fit-content;
        }

        .inner {
            &-wrap {
                position: relative;
                height: 4.8rem;

                input,
                textarea {
                    border: none;
                    background-color: transparent;
                    height: inherit;
                    min-height: unset;
                    padding: 1.3rem 2rem;
                }

                &::after {
                    @include pseudo;
                    background-image: url("../assets/images/input-long-mask.png");
                    @include default-background;
                    background-size: 100% 100%;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    z-index: -1;
                }

                &:has(input:focus),
                &:has(textarea:focus) {
                    &::after {
                        background-image: url("../assets/images/input-long-focus-mask.png");
                    }
                }
            }
        }

        .text {
            display: block;
            color: var(--color-text);
            font-weight: 600;
            margin-bottom: 0.5rem;
            display: none;

            &.spe {
                &::after {
                    content: "*";
                    color: red;
                    display: inline;
                    margin-left: 0.5rem;
                }
            }
        }

        .input {
            position: relative;

            .seepassJS {
                position: absolute;
                right: 1rem;
                top: 50%;
                transform: translateY(-50%);
                color: var(--color-text);
                cursor: pointer;
            }
        }

        input,
        textarea {
            border: 0.1rem solid var(--cl-bd);
            outline: none;
            padding: 1.3rem 2rem;
            width: 100%;
            transition: 0.4s;
            font-size: 1.6rem;
            font-weight: 500;
            // background-color: #FAFAFA;
            border-radius: 100rem;
            height: 4.8rem;

            @include input-placeholder {
                opacity: 0.5;
            }

            &:focus {
                border-color: var(--color-pri);
            }

            @include max(1200px) {
                height: 4rem;
            }

        }

        textarea {
            padding-top: 0.8rem;
            min-height: 13rem;
            border-radius: .8rem;
        }

        // config
        &.per3 {
            width: calc(100% / 3);
        }

        &.per5 {
            width: 50%;

            @include max(500px) {
                width: 100%;
            }
        }

        // select
        .select2-container--default .select2-selection--single {
            border-radius: 100rem;
            background-color: white;
        }

        .select2-container--default .select2-selection--single .select2-selection__placeholder {
            color: #384252;
            font-size: 1.6rem;
            font-weight: 400;
        }

        .select2-container--default.select2-container--open.select2-container--below .select2-selection--single {
            border-radius: 100rem !important;
        }

        .select2-container .select2-selection--single .select2-selection__rendered {
            font-weight: 400;
            font-size: 1.6rem;
            color: #384252;
        }

        .dateTime {
            width: 100%;
        }
    }
}

.box-search {
    position: relative;
    z-index: 1;

    &__flex {
        display: flex;
        align-items: center;
        padding: 0 1rem;
        height: 4.8rem;
        position: relative;

        &::after {
            @include pseudo;
            background-image: url("../assets/images/input-mask.png");
            @include default-background;
            background-size: 100% 100%;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;
        }
    }

    input {
        height: 100%;
        width: 100%;
        background-color: transparent;
        border: none;
        outline: none;
        @include font-second;
    }

    &__btn {
        width: 2.4rem;
        height: 2.4rem;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: inherit;
            height: inherit;
            display: block;
        }
    }

    &:has(input:focus) {
        .box-search__flex {
            &::after {
                background-image: url("../assets/images/input-focus-mask.png");
            }
        }
    }
}


.form-ip {
    .wpcf7-not-valid-tip {
        position: absolute !important;
        font-size: 1.2rem !important;
        color: white !important;
        padding: .4rem .8rem !important;
        border-radius: .5rem !important;
        background-color: var(--color-pri) !important;
        top: 100% !important;
        bottom: unset !important;
        right: .5rem !important;
        width: fit-content !important;
        white-space: nowrap !important;

        &::after {
            @include pseudo;
            width: 0px;
            height: 0px;
            border-style: solid;
            border-width: 0 5px 10px 5px;
            border-color: transparent transparent var(--color-pri) transparent;
            right: 10%;
            bottom: 100%;
        }
    }
}
.wpcf7-form.invalid {
    .wpcf7-response-output {
        background-color: rgba($color: red, $alpha: .1) !important;
        color: red !important;
        width: fit-content !important;
        border: red 0.1rem solid !important;
        margin: 0 auto !important;
        margin-top: 1rem !important;
        border-radius: 0.8rem !important;
    }
}
.wpcf7-form.sent {
    .wpcf7-response-output {
        background-color: rgba($color: green, $alpha: .1) !important;
        color: green !important;
        width: fit-content !important;
        border: green 0.1rem solid !important;
        margin: 0 auto !important;
        margin-top: 1rem !important;
        border-radius: 0.8rem !important;
    }
}