.title-48 {
    @include fz-48;
    color: var(--color-pri);
    line-height: 1.2;
    text-transform: uppercase;
    font-weight: 700;
    &.italic {
        font-style: italic;
    }
    &.bold {
        font-weight: 700;
    }
    &.white {
        color: white;
    }
}
.title-40 {
    @include fz-40;
    color: var(--color-pri);
    line-height: 1.2;
    text-transform: uppercase;
    font-weight: 700;
    &.italic {
        font-style: italic;
    }
    &.bold {
        font-weight: 700;
    }
    &.white {
        color: white;
    }
}

.title-gr {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    &.center {
        align-items: center;
        text-align: center;
    }
    &__top {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 1rem 2.4rem;
    }
    &__bottom {

    }
}

.head-hori {
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    align-items: end;
    gap: 1rem 2.4rem;

    @include max(768px) {
        align-items: center;
        flex-direction: column;
    }

}

.head-verti {
    display: flex;
    flex-direction: column;
    gap: 1rem 2.4rem;


    &.center {
        align-items: center;
        text-align: center;
        margin: 0 auto;

        &.mb-24 {
            margin-bottom: 2.4rem;
        }

        // @include max(768px) {
        //     max-width: 40rem;
        // }
    }
    &.small {
    }
    &.full {
        max-width: unset;
    }

    .mona-content {
        p {
            margin: 0;
            color: var(--color-des);

            &.fz-24 {
                @include fz-24;
            }
        }
    }

}

.txt-pri {
    color: var(--color-pri);
}
.txt-second {
    color: var(--color-second);
}