// Fade
[data-aos="fade-up-cus"] {
    transform: translate(0, 4rem);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: translate(0, 0);
        opacity: 1;
    }
}

[data-aos="fade-down-cus"] {
    transform: translate(0, -4rem);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: translate(0, 0);
        opacity: 1;
    }
}

[data-aos="fade-left-cus"] {
    transform: translate(4rem, 0);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: translate(0, 0);
        opacity: 1;
    }
}

[data-aos="fade-right-cus"] {
    transform: translate(-4rem, 0);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: translate(0, 0);
        opacity: 1;
    }
}
// Zoom
[data-aos="zoom-in-cus"] {
    transform: scale(0.9);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: scale(1);
        opacity: 1;
    }
}

[data-aos="zoom-out-cus"] {
    transform: scale(1.1);
    opacity: 0;
    transition-property: transform, opacity;
    &.aos-animate {
        transform: scale(1);
        opacity: 1;
    }
}
// Animation
.blurUp {
    transform: translateY(4rem);
    opacity: 0;
    transition: 0.8s;
    transition-delay: var(--dl);
}
.fadeUp {
    transform: translateY(2rem);
    opacity: 0;
    transition: 0.8s;
    transition-delay: var(--dl);
}
.is-inview {
    .fadeUp {
        transform: translateY(0rem);
        opacity: 1;
    }
}