.mobile-wr {
    height: 100%;
    display: flex;
    flex-direction: column;

    .mobile-nav {
        height: 100%;
        display: flex;
        flex-direction: column;
        .menu-nav {
            padding-top: 2rem;
            height: 100%;

            &>.menu-list>.menu-item a {
                padding-right: 1rem !important;
            }

            &>.menu-list>.menu-item>a {
                background-color: transparent;
                padding: .9rem .8rem;
                border-bottom: none;
                // @include font-second;
                // font-size: 2rem;
                text-transform: uppercase;
                font-weight: 700;
                // border-radius: .6rem;
                position: relative;
                overflow: hidden;
                transition: 0.4s ease-in-out;
                border-radius: .6rem;

                &::after {
                    @include pseudo;
                    display: block;
                    height: 100%;
                    width: 100%;
                    left: 0;
                    top: 0;
                    z-index: -1;
                    transform: translateY(100%);
                    pointer-events: none;
                    background-color: var(--color-pri);
                    transition: 0.4s;
                    border-radius: .6rem;
                }

                &::before {
                    @include pseudo;
                    height: 100%;
                    width: 100%;
                    display: block;
                    background-image: url("../assets/images/item-dec.png");
                    @include default-background;
                    pointer-events: none;
                    border-radius: .6rem;
                }

                @include hover {
                    background-color: #FEF9F5;
                    color: var(--color-pri);
                }
            }

            &>.menu-list {
                &>.menu-item {
                    &.active {
                        >a {
                            background-color: var(--color-pri);
                            color: white;

                            &::after {
                                transform: translateY(0);
                            }
                        }
                    }
                }
            }

            &>.menu-list>.menu-item {
                padding: 0 1rem;
            }

            .menu-item.active {
                >a {
                    font-weight: 700;
                }
            }

            &>.menu-list>.menu-item>.menu-list>.menu-item>.menu-list>.menu-item:first-child>a {
                padding-top: 0 !important;
            }

            &>.menu-list>.menu-item>.menu-list>.menu-item>.menu-list>.menu-item:last-child>a {
                padding-bottom: 0 !important;
            }

            &>.menu-list>.menu-item>.menu-list>.menu-item>.menu-list {
                padding-bottom: 1rem;
            }
        }
    }

    .mobile-nav .menu-item>.menu-list>.menu-item>.menu-list::after {
        top: 0 !important;
    }
}

.mobile-nav .menu-nav > .menu-list > .menu-item:has(.current-menu-item) > a {
    color: white !important;
} 