:root {
    --size-hd: 11.2rem;
    --size-hd-top: 4rem;
    --size-hd-bot: 7.2rem;
    --pd-sc: 8rem;
    --pd: 4rem;
    --pd-42: clamp(2rem, calc(1.1803rem + 1.6393vw), 4rem);
    --pd-32: clamp(2rem, calc(1.5902rem + 0.8197vw), 3rem);
    --pd-3-15: clamp(1.5rem, calc(0.8852rem + 1.2295vw), 3rem);
    // border
    --radius: 1rem;
    --radius-xl: 3.2rem;
    --radius-sm: 0.5rem;
    --cl-bd: #E2E8F0;
    --cl-bdw: rgba(255,255,255,.2);
    // transition
    --transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
    // distance
    --container: 123rem;
    --recontainer: calc(100vw - (100vw - var(--container))/2);
    --half: calc((100vw - var(--container))/2);
    // color
    --color-text: #111111;
    --color-des:#111111;
    --color-des2:#616161;
    --color-pri: #0B00CF;
    --color-second: #D7043F;
    --color-third: #071836;
    --color-red: #DD3C27;
    // shadow
    --sdow:0px 4px 20px rgba(0, 0, 0, 0.08);
    --dr-sdow: drop-shadow(0px 8px 24px rgba(0, 0, 0, 0.1));

    // filter: ;
    --filter-pri: brightness(0) saturate(100%) invert(18%) sepia(46%) saturate(5672%) hue-rotate(346deg) brightness(91%) contrast(93%);
    --filter-second: brightness(0) saturate(100%) invert(9%) sepia(42%) saturate(2050%) hue-rotate(192deg) brightness(92%) contrast(100%);
    // linear
    --color-linear2: linear-gradient(90deg, #D7043F 0%, #7A0627 100%);
    --color-linear: linear-gradient(90deg, #0B00CF 0%, #1A1F91 100%);
    @media (max-width: 1200px) {
        --pd-sc: 4.8rem;
        --radius: 1rem;
        --radius-xl: 1.6rem;
        --size-hd: 7rem;
        --pd: 2rem;
        --recontainer: calc(100% - 1.5rem);
        --half: 1.5rem;
        --rds:4rem;
    }
}

//Responsive
$res-mobile: 1199px;
$res-mobile-min: 1201px;
$mobile: 500px;