// 
.collapse-item.active {
    .collapse-head {
        pointer-events: none;
    }
}

.collapse-head {
    cursor: pointer;
}

.collapse-body {
    display: none;
}

.collapse-item {
    transition: 0.4s;
    padding: 1.6rem 0;

    &:not(:first-child) {
        border-top: 0.1rem solid #F5F5F5;
    }

    .collapse-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;
        @include fz-24;
        @include font-second;

        .icon {
            width: 2.4rem;
            height: 2.4rem;
            flex-shrink: 0;
            transition: 0.4s;

            img {
                width: inherit;
                height: inherit;
            }
        }
    }

    .collapse-body {
        color: #525252;
        font-size: 1.4rem;
    }

    &.active {

        .collapse-head {

            .icon {
                transform: rotate(180deg);
            }
        }
    }
}

.cls-head {
    cursor: pointer;
}

.cls-body {
    display: none;
}

.rating {
    display: flex;
    gap: 0.5rem;

    .stars {
