        white-space: nowrap;
    }
}

// text animation
.text-hori {
    .char {
        transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
        transform: translateX(4rem);
        opacity: 0;
    }

    &.is-inview {
        .char {
            transform: translateX(0);
            opacity: 1;
        }
    }
}

.text-verti {
    .char {
        transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
        transform: translateY(4rem);
        opacity: 0;
    }

    &.is-inview {
        .char {
            transform: translateY(0);
            opacity: 1;
        }
    }
}

.text-scale {
    .char {
        transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
        transform: scale(0);
        opacity: 0;
    }

    &.is-inview {
        .char {
            transform: scale(1);
            opacity: 1;
        }
    }
}

.text-blur {
    .char {
        transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
        filter: blur(1rem);
        opacity: 0;
    }

    &.is-inview {
        .char {
            filter: blur(0);
            opacity: 1;
        }
    }
}

.text-jump {
    .char {
        transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
        transform: translateY(4rem) scale(0.5);
        opacity: 0;
    }

    &.is-inview {
        .char {
            transform: translateY(0) scale(1);
            opacity: 1;
        }
    }
}

