// Hover Img
.hover-img-x {
    .img-inner {
        img {
            width: calc(100% + 4rem) !important;
            transition: 0.4s;
            border-radius: 0;
            max-width: unset;
            pointer-events: none;
        }

        @include hover {
            img {
                transform: translate(calc(-1*(50% + 2rem)), -50%) !important;
            }
        }
    }
}

.hover-img-y {
    .img-inner {
        img {
            height: calc(100% + 4rem) !important;
            transition: 0.4s all;
            border-radius: 0;
            max-width: unset;
            object-position: top;
        }

        @include hover {
            img {
                transform: translate(-50%, -50%) scale(1.02) !important;
            }
        }
    }
}

.hover-img-scale {
    .img-inner {
        img {
            transition: 0.4s all;
        }

        @include hover {
            img {
                transform: translate(-50%, -50%) scale(1.05) !important;
            }
        }
    }
}

.hover-img-rotate {
    .img-inner {
        img {
            transition: 0.4s;
            border-radius: 0;
            max-width: unset;
        }

        @include hover {
            img {
                transform: translate(-50%, -50%) scale(1.05) rotate(2deg) !important;
            }
        }
    }
}

// LayoutTwoBlock
.layout-two {
    --w-side: 37.7rem;

    &.rev {
        flex-direction: row-reverse;
    }

    .side-open {
        position: sticky !important;
        top: 20%;
        margin-left: auto;
        margin-right: -1.5rem;
        margin-bottom: -4rem;
        transform: none !important;

        &-wrap {
            i {
                color: white;
            }
        }
    }

    &-l {
        width: calc(100% - var(--w-side));

        .wrapper {
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }
    }

    &-r {
        width: var(--w-side);

        .wrapper {
            display: flex;
            flex-direction: column;
            gap: 2.4rem;

            @include min(1201px) {
                position: sticky;
                top: 8rem;
            }
        }
    }

    @include max(1200px) {
        &-l {
            width: 100%;
        }

        &-r {
            width: 100%;
        }
    }
}

