.twoSplide {
    overflow: hidden;
    padding-bottom: 2rem;

    .splide {
        &:not(:first-child) {
            margin-top: 1.6rem;

            @include max(1200px) {
                margin-top: 1.2rem;
            }
        }

        .splide__track {
            overflow: visible;
        }
    }

    &-it {
        position: relative;
        border-radius: 2.4rem;
        overflow: hidden;
        border: 0.1rem solid transparent;
        transition: 0.4s;
        background-color: white;

        @include max(800px) {
            border-radius: .8rem;
        }

        .img {
            @include imgPd(409.29, 571.43);

            img {
                @include imgCover;
            }
        }

        &.has-video {
            position: relative;

            &::after {
                @include pseudo;
                height: 100%;
                width: 100%;
                background: linear-gradient(180deg, rgba(11, 0, 207, 0) 0%, #1A1F91 100%);
                top: 0;
                left: 0;
                pointer-events: none;
            }

            &::before {
                content: "\f04b";
                position: absolute;
                font-family: "Font Awesome 6 Pro";
                width: 4rem;
                height: 4rem;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
                cursor: pointer;
                color: whitesmoke;
                font-weight: 700;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 4rem;
                pointer-events: none;

                @include max(800px) {
                    width: 3rem;
                    height: 3rem;
                    font-size: 3rem;
                }
            }
        }
    }
}

