// Found
.found {
    position: fixed;
    width: 100vw;
    z-index: 168;
    transition: 0.4s;
    opacity: 0;
    // transform: translateY(-100%);
    pointer-events: none;
    max-width: 40rem;
    margin: 0 auto;
    transform: translateY(-50%) translateX(-50%);
    left: 50%;
    top: 50%;

    // @include min(721px) {
    //     display: none;
    // }

    @include min(1201px) {
        width: calc(100vw);
    }


    &.open {
        // transform: translateY(0);
        opacity: 1;
        pointer-events: visible;

        .found-f {
            .tt {
                opacity: 1;
                transition: 0.4s;
                transition-delay: 0.4s;
            }

            .found-form {
                opacity: 1;
                transition: 0.4s;
                transition-delay: 0.6s;
            }
        }

        .found-x {
            transition: 0.4s;
            transform: scale(1);
            transition-delay: 0.4s;

            @include hover {
                transition-delay: 0s;
                background-color: #488365;
                color: white;
            }
        }
    }

    &-over {
        height: 100%;
        width: 100%;
        position: fixed;
        height: calc(100vh - var(--size-hd));
        left: 0;
        width: 100vw;
        transition: 0.4s;
        background-color: rgba($color: #000000, $alpha: 0.5);
        z-index: -1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, calc(-50% + var(--size-hd)/2));
    }

    &-wr {
        background-color: white;
        padding: 4.8rem;
        padding: 1.5rem 0;
        // border-bottom: 0.1rem solid var(--cl-bd);
        box-shadow: var(--sdow);
    }

    &-f {
        max-width: 70rem;
        margin: auto;

        .tt {
            @include fz-32;
            font-weight: 600;
            margin-bottom: 1rem;
            opacity: 0;
            display: none;
        }
    }

    &-form {
        height: 4.8rem;
        position: relative;
        opacity: 0;

        @include max(500px) {
            height: 4rem;
        }

        input {
            width: 100%;
            height: 100%;
            border: 0.1rem solid #E0E0E0;
            transition: 0.4s;
            padding: 1rem;
            font-size: 1.6rem;
            border-radius: 1rem;

            &:focus {
                border-color: var(--color-pri);
            }
        }

        button {
            width: 3.8rem;
            height: 3.8rem;
            @include mid-flex;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: .5rem;
            transition: 0.4s;
            border-radius: 1rem;

            @include max(500px) {
                width: 4rem;
                height: 4rem;
            }

            img {
                width: 2rem;
                height: 2rem;
            }

            @include hover {
                background-color: var(--color-pri);

                img {
                    filter: brightness(0) invert(1);
                }
            }
        }
    }

    &-x {
        position: absolute;
        width: 2.3rem;
        height: 2.3rem;
        right: 0;
        top: 0;
        cursor: pointer;
        @include mid-flex;
        border-radius: 0 0 0 10rem;
        transform: scale(0);
        z-index: 100;
        background-color: var(--color-pri);

        i {
            font-size: 0.8rem;
            transform: translate(30%, -30%);
            color: white;
        }
    }
}

