// Copy
.copyJS {
    position: relative;

    &::before {
        content: attr(data-text);
        position: absolute;
        bottom: calc(100% + 1rem);
        transform: translate(-50%, 0);
        left: 50%;
        font-size: 1.4rem;
        background-color: var(--color-pri);
        color: white;
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        opacity: 0;
        transition: 0.4s;
        pointer-events: none;
        white-space: nowrap
    }

    &::after {
        @include pseudo;
        width: 0px;
        height: 0px;
        border-style: solid;
        border-width: 1rem .5rem 0 .5rem;
        border-color: var(--color-pri) transparent transparent transparent;
        bottom: calc(100%);
        left: 50%;
        transform: translate(-50%, 0);
        opacity: 0;
        transition: 0.4s;
        pointer-events: none;
    }

    &.active {
        &::before {
            opacity: 1;
        }

        &::after {
            opacity: 1;
        }
    }
}

// Blur
