    mask-image: linear-gradient(to left, rgba(0, 0, 0, 0) 0%, black 5%, black 95%, rgba(0, 0, 0, 0) 100%);
}

.blur-tb {
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, black 5%, black 95%, rgba(0, 0, 0, 0) 100%);
}



.copyJS {
    position: relative;
    mask-image: none;
    background-color: transparent !important;
    overflow: visible;

    .txt {
        background-color: var(--color-pri);
        mask-image: url("../assets/images/btn-mask.png");
        mask-size: 100% 100%;
        mask-repeat: no-repeat;
        mask-position: center;
    }

    @include hover {
        .txt {
            background-color: white !important;
        }
    }

    &::before {
        content: "Copied";
        font-size: 1.4rem;
        padding: .2rem .5rem;
        border-radius: .5rem;
        background-color: var(--color-pri);
        width: fit-content;
        color: white;
        position: absolute;
        bottom: calc(100% + 1rem);
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        pointer-events: none;
        transition: 0s;
    }

    &::after {
        @include pseudo;
        width: 0px;
        height: 0px;
        border-style: solid;
        border-width: 11px 5.5px 0 5.5px;
        border-color: var(--color-pri) transparent transparent transparent;
        transform: rotate(0deg);
        bottom: calc(100%);
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        pointer-events: none;
        transition: 0s;
    }

    &.active {
        &::before {
            opacity: 1;
        }

        &::after {
            opacity: 1;
        }
    }
}


