.mona-content {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        line-height: 1.3;
        color: var(--color-text);
    }

    h1 {
        font-size: 2em;
    }

    h2 {
        font-size: 1.89em;
    }

    h3 {
        font-size: 1.25em;
    }

    h4 {
        font-size: 1.125em;
    }

    h5 {
        font-size: 1em;
    }

    h6 {
        font-size: 0.85em;
    }

    p {
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
        margin: 1em 0;
        
    }

    ul,
    ol {
        margin: 1em 0;
        list-style-position: inside;
    }

    ul ul,
    ul ol,
    ol ul,
    ol ol {
        margin-left: 1em;
    }

    ul {
        list-style-type: disc;
    }

    ol {
        list-style-type: decimal;
    }

    ul ul,
    ol ul {
        list-style-type: circle;
    }

    ol ol,
    ul ol {
        list-style-type: lower-latin;
    }

    img,
    iframe {
        max-width: 100% !important;
        height: auto;
    }

    blockquote {
        color: white;
        padding: 1rem 2rem;
        quotes: "\201C" "\201D" "\2018" "\2019";
        position: relative;
        border-left: 0.2rem solid var(--color-pri);

        img {
            aspect-ratio: 129/104;
            width: 20%;
            position: absolute;
            left: 5%;
            top: 0;
            border-radius: var(--radius);
        }

        p {
            display: inline;
            margin: 0;
            position: relative;
            z-index: 2;
            font-size: 1.8rem;
            color: var(--color-text);
            font-style: italic;
            font-weight: 500;
        }

        i {
            color: #737373;
            display: block;
            margin-top: 1rem;
        }
    }

    figure:not(:has(img)) {
        margin: 1rem 0;
        padding: 2rem;
        border-radius: var(--radius);
        background-color: var(--color-second);
        color: white;
        font-size: 1.6rem;
        text-align: center;

        p {
            color: white;
            margin: 0;
            font-weight: 700;
        }

        small {
            font-size: 0.8em;
            font-style: italic;
        }
    }

    table {
        border-collapse: collapse;
        max-width: 100%;

        th,
        td {
            border-right: 1px solid var(--cl-bd);
            border-bottom: 1px solid var(--cl-bd);
            padding: 5px 10px;
            vertical-align: middle;
        }

        th:first-child,
        td:first-child {
            border-left: none !important;
        }

        th:last-child,
        td:last-child {
            border-right: none !important;
        }

        tbody {
            tr:last-child {
                td {
                    border-bottom: none !important;
                }
            }
        }
    }

    .mona-youtube-wrap {
        position: relative;
        height: 0;
        padding-top: 56.25%;

        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    .mona-table-wrap {
        overflow: auto;
    }

    p {
        color: var(--color-des);

        @media screen and (max-width: 768px) {
            font-size: 1.4rem;
        }
    }

    h1,
    h2,
    h3,
    h4,
    h5 {        
        font-family: "SVN-Saycheez", sans-serif;
    }

    img {
        max-width: 100%;
    }

    figure {
        margin: 1rem 0;

        img {
            margin: 0;
            mask-image: url("../assets/images/mona-mask.png");
            mask-size: 100% 100%;
            mask-repeat: no-repeat;
            mask-position: center;
        }

        figcaption {
            color: var(--color-des);
            font-weight: 500;

            .iwt {
                --iz: 1.6rem;
                --gap: .6rem;
            }

            p {
                margin: 0;
                font-size: 1.4rem;
            }
        }
    }
    ul {
        padding-left: 0 !important;
    }

    li {
        p {
            display: inline;
        }
    }
}