.back-to-top {
    width:4.8rem;
    height:4.8rem;
    position: fixed;
    bottom: 6rem;
    right: 3rem;
    z-index: 7;
    transform: translateX(3rem);
    opacity: 0;
    transition: 0.5s linear;
    cursor: pointer;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    
    @include max(768px) {
        right: 1.5rem;
    }

    @include max(600px) {
        width: 3rem;
        height: 3rem;
    }

    &.active {
        visibility: visible;
        transform: translateX(0);
        opacity: 1;
    }

    .triangle {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: inherit;
            height: inherit;
            display: block;
        }
    }
}

// side
.sidefix {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: 3rem;
    bottom: 12rem;
    z-index: 3;
    gap: 1rem;
    // display: none;

    @include max(768px) {
        right: 1.5rem;
    }

    @include max(600px) {
        bottom: 11rem;
    }

    &-item {
        position: relative;
        padding: 0rem;
        border-radius: 100rem;
        transition: 0.4s;
        @include mid-flex;
        cursor: pointer;

        @include hover {
            box-shadow: var(--sdow);
        }

        &::before {
            @include pseudo;
            height: 100%;
            width: 100%;
            border-radius: inherit;
            animation-delay: 2s;
        }

        &:hover {
            .sidefix-sub {
                transform: scale(1);
            }
        }

        &:nth-child(3) {

            .phone-number {
                position: absolute;
                right: 0;
                height: 4.8rem;
                padding: 0.6rem 1.2rem;
                padding-top: 0.5rem;
                line-height: 1;
                display: flex;
                justify-content: center;
                flex-direction: column;
                background: white;
                color: black;
                font-weight: 700;
                z-index: -1;
                overflow: hidden;
                transition: 0.4s;
                width: 1rem;
                white-space: nowrap;
                opacity: 0;
                line-height: 1.2;
                border: 0.1rem solid var(--color-pri);
                transform: translateY(-50%);
                top: 50%;
                border-radius: 100rem;
                font-size: 1.4rem;
                text-align: right;
                padding-right: 4rem;
                display: none;

                @include max(600px) {
                    display: none;
                }
            }

            &:hover {
                .phone-number {
                    width: 15rem;
                    opacity: 1;

                    .txt {
                        font-size: 1.2rem;
                    }
                }
            }
        }
    }

    &-sub {
        position: absolute;
        background: var(--color-pri);
        right: calc(100% + 1rem);
        padding: 1.5rem 1rem;
        border-radius: 1rem;
        bottom: 0;
        min-width: 20rem;
        transform: scale(0);
        transition: 0.4s;
        transform-origin: right bottom;

        &::before {
            @include pseudo;
            left: 100%;
            width: 1rem;
            height: 100%;
            top: 0;
        }

        &::after {
            @include pseudo;
            @include borderArrow(right, 0.5rem, var(--color-pri));
            left: 100%;
            bottom: 1.5rem;
        }

        &-item {
            display: flex;
            align-items: center;
            gap: 1rem;

            &:not(:first-child) {
                margin-top: 1.2rem;
            }

            img {
                width: 3.2rem;
                height: 3.2rem;
            }

            .text {
                font-size: 1.4rem;
                line-height: 1.2;

                &-name {
                    display: block;
                    color: white;
                    font-weight: 500;
                }

                &-link {
                    color: white;

                }

                @include hover {
                    .text-link {
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    &-link {
        width: 4.8rem;
        height: 4.8rem;
        @include mid-flex;
        position: relative;
        z-index: 2;

        img {
            width: 4.8rem;
            height: 4.8rem;
        }

        @include max(600px) {
            width: 3rem;
            height: 3rem;

            img {
                width: 3rem;
                height: 3rem;
            }
        }
    }
}