.social {
    &-list {
        @include flex;
        align-items: center;
        gap: 1.2rem;
    }

    &-link {
        display: block;
        width: 4.8rem;
        height: 4.8rem;
        transition: 0.3s ease-in-out;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 100rem;
        border: 0.2rem solid var(--color-text);
        @include max(768px) {
            width: 3rem;
            height: 3rem;
        }

        &:hover {
            border-color: var(--color-pri);
            background-color: var(--color-pri);
            img {
                filter: brightness(0) invert(1);
            }
        }

        img {
            width: 100%;
            height: 100%;
            display: block;
            border-radius: 0;
            transition: 0.4s;
        }


    }
}