.ft {
    background-color: #F5F7FF;
    border-radius: var(--radius-xl) var(--radius-xl) 0 0;
    @include min(1301px) {
        border-radius: 7rem 7rem 0 0;
    }
    box-shadow: 4.8px -4.8px 12px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 2;
    .ft-top {
        padding: var(--pd-sc) 0;
    }

    .ft-signature {
        padding-bottom: 4rem;
        text-align: center;
    }
    .ft-logo {
        max-width: 14.6rem;
        .custom-logo-link {
            width: 100%;
            display: block;

            img {
                width: 100%;
                height: auto;
                display: block;
            }
        }
    }
    .ft-flex {
        --cg: 0;
        --rg: 2.4rem;
        display: grid;
        grid-template-columns: calc(7/12*100%) calc(5/12*100%);
        grid-template-rows: repeat(2, 1fr);
        @include max(600px) {
            display: flex;
            max-width: 40rem;
            margin: 0 auto;
        }
    }
    .ft-info {
        color: var(--color-text);
        @include fz-18;
        .lines {
            .name {
                font-weight: 700;
                display: inline;
            }
            .val {
                display: inline;
            }
        }
    }
    .ft-item {
        .wrapper {
            .ft-logo {
                margin-bottom: 2rem;
            }
        }
        &:nth-child(1) {
            grid-row: span 2 / span 2;
        }
        &:nth-child(3) {
            grid-column-start: 2;
        }
    }
    .ft-tt {
        @include fz-24;
        font-weight: 700;
        color: var(--color-pri);
        margin-bottom: 1rem;
    }
}