.hd {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: 0.4s;
    z-index: 141;
    height: var(--size-hd);
    background-color: white;
    border-bottom: 0.1rem solid var(--cl-bd);
}

.hd.sticky {
    @include min(1201px) {
        // --size-hd: 8rem;

        .hd-logo {
            .custom-logo-link {
                // width: 20rem;
            }
        }
    }
}



.hd-wr {
    height: 100%;
    z-index: 141;
    position: relative;
}

.hd-top {
    height: var(--size-hd-top);
    background-color: var(--color-pri);
    display: flex;
    justify-content: center;
    align-items: center;
    .hd-text {
        font-size: 1.6rem;
        width: fit-content;
        display: block;
    }
    @include max(1200px) {
        height: 2rem;
        .hd-text {
            font-size: 1.4rem;
        }
    }
}

.hd-bot {
    height: var(--size-hd-bot);
    @include max(1200px) {
        height: 5rem;
    }
}



.hd-text {
    font-size: 1.8rem;
    color: white;
    text-transform: uppercase;
    font-weight: 700;
}

.hd-flex {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: space-between;
    gap: 2.4rem;
    position: relative;


    @include max(600px) {
        .hd-logo {
            position: static;
            transform: none;
            order: -1;
        }

        .hd-lg {
            margin-left: auto;
        }

        .hd-act {
            margin-left: unset;
        }
    }
}

.hd-nav {
    height: 100%;

    @include max(1200px) {
        display: none;
    }

    .menu-nav {
        height: 100%;

        .menu-list {
            >.menu-item {
                // white-space: nowrap;
                height: 100%;

                a {
                    padding: 1rem 0;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}

.hd-lg {
    height: 4rem;
    flex-shrink: 0;
    position: relative;
    cursor: pointer;
    z-index: 2;
    transition: 0.4s;
    // font-weight: 700;
    // @include font-second;

    @include hover {
        // background-color: var(--color-second);

        >.hd-lg-item {
            color: var(--color-pri);
        }
    }

    @include max(1200px) {
        height: 4rem;
        width: 4rem;
        padding: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: .6em;
        background-color: var(--color-pri);


        .hd-lg-item {
            img {
                width: 2rem;
                height: 2rem;
                display: block;
            }

            .txt {
                color: var(--color-pri);
                color: white;
                line-height: 1;
                transform: translateY(5%);
                font-weight: 600;
            }

            .ic {
                display: none;
            }
        }

        >.hd-lg-item {
            color: white;
        }

        .hd-lg-drop {
            left: 50%;
            transform: translateX(-50%);
            top: calc(100% + 2rem);
            width: calc(100% - 1rem);

            &::after {
                border-radius: .8rem;
                height: 4rem;
                background-color: var(--color-pri);

            }

            >.hd-lg-item {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: var(--color-pri);

                .txt {
                    color: white;
                }
            }
        }
    }

    >.hd-lg-item {
        // color: white;
    }

    &-item {
        height: 100%;
        display: flex;
        align-items: center;
        gap: 0.4rem;

        img {
            width: 2.4rem;
            height: 2.4rem;
            flex-shrink: 0;
        }

        .txt {
            transition: 0.4s;
            line-height: 1;
        }

        .ic {
            width: 1.6rem;
            height: 1.6rem;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.4rem;
            // display: none;
        }

        @include min(1201px) {
            padding: 0 1rem;
            height: 4rem;
        }

        @include max(1200px) {
            img {
                display: none !important;
            }

            .txt {
                // display: none;
            }
        }
    }

    &-drop {
        position: absolute;
        width: 100%;
        top: calc(100% + .5rem);
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        transition: 0.4s;
        transform: translateY(-1rem);
        opacity: 0;
        visibility: hidden;
        pointer-events: none;

        &::before {
            @include pseudo;
            height: 0.5rem;
            width: 100%;
            bottom: 100%;
            left: 0;
        }

        &::after {
            @include pseudo;
            width: calc(100% + 1rem);
            height: calc(100% + 1rem);
            background-color: white;
            box-shadow: var(--sdow);
            z-index: -1;
            @include mid;
            border-radius: var(--rs);
        }

        @include min(1201px) {
            &::after {
                display: none;
            }
        }

        @include max(1200px) {
            transition: 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            transform: translate(-50%, -100%) rotate(-90deg) !important;
        }

        .hd-lg-item {
            background-color: white;
            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

            img {}

            @include hover {
                .txt {
                    color: var(--color-pri);
                }
            }
        }
    }

    &:hover {
        .hd-lg-drop {
            transform: translateY(0);
            opacity: 1;
            visibility: visible;
            pointer-events: visible;

            @include max(1200px) {
                transform: translate(-50%, 0) rotate(0deg) !important;
            }
        }
    }
}

.hd-logo {

    .custom-logo-link {
        display: flex;
        transition: 0.4s;
        width: 7rem;

        img {
            max-width: 100%;
            height: auto;
            transition: 0.4s;
        }
    }
}

.hd-act {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    gap: 1rem;

    &-link {
        height: 5rem;
        padding: 0 1.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.4s;

        @include hover {
            color: white;
            background-color: var(--color-second)
        }
    }

    &-it {
        width: 5rem;
        height: 5rem;
        transition: 0.4s;
        cursor: pointer;

        .icon {
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        @include hover {
            background-color: var(--color-second);

            .icon {
                img {
                    filter: brightness(0) invert(1);
                }
            }
        }
    }

    .btn-box {
        @include max(1200px) {
            display: none;
        }
    }
}

.hd-srch {
    width: 4.8rem;
    height: 4.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    cursor: pointer;
    border-radius: .6em;
    transition: 0.4s;

    img {
        width: 50%;
        height: 50%;
    }

    @include hover {
        background-color: var(--color-pri);

        img {
            filter: brightness(0) invert(1);
        }
    }
}

.hd-contact {
    width: 4rem;
    height: 4rem;
    border-radius: .6em;
    background-color: var(--color-pri);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;

    i {
        animation: phoneRing 1s linear infinite;
    }
}

@include max(1300px) {
    .hd {
        .hd-logo {
            .custom-logo-link {
                width: 20rem;
            }
        }
    }
}

@include max(1200px) {
    .hd {
        top: 0;

        &::after {
            opacity: 1;
            display: none;
        }

        background-color: white;

        .box-search {
            display: none;
        }
    }

    .hd-logo {
        position: static;
        transform: none;
        display: flex;
        justify-content: center;
        align-items: center;

        // filter:var(--filter-pri);
        .custom-logo-link {
            width: 15rem;
        }
    }

    .hd-srch {
        width: 4rem;
        height: 4rem;
        background-color: var(--color-pri);

        img {
            filter: brightness(0) invert(1);
        }
    }

    .hd-act {
        margin-left: auto;
        gap: 1rem;

        &-it {
            width: 4rem;
            height: 4rem;
            background-color: var(--color-second);

            img {
                filter: brightness(0) invert(1);
            }
        }

        &-link {
            display: none;
        }
    }


}

@include max(1024px) {
    .hd-flex {
        gap: 1rem;
    }



    .hd-cate {
        &-open {
            height: 4rem;
            padding: .5rem 1.2rem;
        }
    }
}

@include max(768px) {
    .hd-find {
        display: none;
    }

    .hd-logo {
        .custom-logo-link {
            width: 12rem;
        }
    }
}