.swiper-wrapper.row {
    flex-wrap: nowrap;
}

.swiper.rows {
    .col {
        margin-bottom: 0;
    }
}

.swiper-container {
    overflow: hidden;
}

.swiper-slide {
    height: initial;
    overflow: hidden;
}

.swiper-pagination {
    position: static;
    margin-top: 1.6rem;

    &-bullet {
        --circle: -0.4rem;
        background-color: var(--color-pri);
        opacity: 1;
        position: relative;
        margin: 0 0.3rem !important;
        transition: 0.4s;
        height: 1.6rem;
        width: 1.6rem;
        opacity: 0.2;


        &-active {
            background-color: var(--color-pri);
            opacity: 1;

            &::before {
                opacity: 1;
            }
        }
    }
    &.white {
        .swiper-pagination-bullet {
            background-color: white !important;
            
        }
    }
}

.swiper-control {
    display: flex;
    align-items: center;
    gap: 1.6rem;
    --size: 6rem;

    &-btn {
        aspect-ratio: 6/6.4;
        width: var(--size);
        flex-shrink: 0;
        @include mid-flex;
        cursor: pointer;
        @include transitionRe;
        z-index: 5;
        position: relative;
        font-size: 1.6rem;

        &::before {
            @include pseudo;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            background-image: url("../assets/images/arrow-left.png");
            @include default-background;
            transition: 0.4s;
        }

        &::after {
            @include pseudo;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            mask-image: url("../assets/images/arrow-left-bg.png");
            mask-size: 100% 100%;
            mask-repeat: no-repeat;
            mask-position: center;
            z-index: -1;
            background-color: white;
            transition: 0.4s;
        }

        >* {
            position: relative;
            z-index: 2;
        }

        &.swiper-next {
            &::before {
                background-image: url("../assets/images/arrow-right.png");
            }

            &::after {
                mask-image: url("../assets/images/arrow-right-bg.png");
            }
        }

        &.swiper-button-disabled {
            pointer-events: none;
            opacity: 0.5;
            display: none;
        }


        @include hover {
            &:not(.swiper-button-disabled) {
                &::before {
                    filter: brightness(0) invert(1);
                }

                &::after {
                    background-color: var(--color-pri);
                }
            }

            i {
                color: white;
            }
        }
    }

    &.white {
        .swiper-control-btn {
            &::before {
                filter: brightness(0) invert(1);
            }
            &::after {
                background-color: transparent;
            }
        }

        &.mobile-plus {
            .swiper-control-btn {
                @include max(1350px) {
                    background-color: white;
                    border-color: var(--color-pri);
                    color: var(--color-pri);
                }
            }

        }
    }

    &.posi {
        .swiper-control-btn {
            @include posi-vertical;
        }

        &.minus {
            .swiper-prev {
                right: calc(100% + 4rem);

                @include max(1350px) {
                    right: unset;
                    left: calc(var(--size) / 2 * -1 + 2.4rem);
                    display: none;
                }
            }

            .swiper-next {
                left: calc(100% + 4rem);

                @include max(1350px) {
                    left: unset;
                    right: calc(var(--size) / 2 * -1 + 2.4rem);
                    display: none;
                }
            }

            &.mobile-plus {
                @include max($res-mobile) {
                    .swiper-prev {
                        left: calc((var(--size) / 2 * -1) + (1.5rem / 2));
                    }

                    .swiper-next {
                        right: calc((var(--size) / 2 * -1) + (1.5rem / 2));
                    }
                }
            }
        }

        &.midle {
            .swiper-prev {
                left: calc(var(--size) / 2 * -1);

                @include max(1320px) {
                    left: 0;
                }
            }

            .swiper-next {
                right: calc(var(--size) / 2 * -1);

                @include max(1320px) {
                    right: 0;
                }
            }
        }

        &.plus {
            .swiper-prev {
                left: 1rem;
            }

            .swiper-next {
                right: 1rem;
            }

        }

        &.mobile-plus {
            @include max($res-mobile) {
                .swiper-prev {
                    left: calc((var(--size) / 2 * -1) + (1.5rem / 2));
                }

                .swiper-next {
                    right: calc((var(--size) / 2 * -1) + (1.5rem / 2));
                }
            }
        }
    }

}



.swiper-pagination {
    &.swiper-pagination-progressbar {
        height: 0.2rem !important;
        background-color: #E6E6E6 !important;
    }

    .swiper-pagination-progressbar-fill {
        background-color: #000000 !important;
        position: relative;
        display: block;
    }
}


// =================
.swiper-controls {
    .swiper-control-btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        aspect-ratio: 68/248;
        width: 6.8rem;
        border-radius: 0;
        background-color: transparent;
        border: none;

        @include max(1200px) {
            width: 4rem;
        }

        @include max(768px) {
            width: 2.4rem;

            i {
                font-size: 1rem !important;
            }
        }

        &::after {
            @include pseudo;
            mask-image: url("../assets/images/num.svg");
            mask-size: 100% 100%;
            mask-repeat: no-repeat;
            mask-position: center;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            background-color: var(--color-pri);
        }

        i {
            font-size: 2.4rem;
            color: white;
        }

        &.swiper-button-disabled {
            &::after {
                background-color: var(--color-text);
                opacity: 0.2;
            }
        }

        &.swiper-prev {
            left: 0;

            &::after {
                transform: scaleX(-1);
            }
        }

        &.swiper-next {
            right: 0;
        }
    }

}