.popup {
    position: fixed;
    @include full;
    z-index: 999;
    visibility: hidden;
    pointer-events: none;

    &.open {
        visibility: visible;
        pointer-events: visible;

        .popup-overlay {
            opacity: 1;
            visibility: visible;
            height: 100%;
        }

        .popup-main {
            opacity: 1;
            visibility: visible;
            @include transition(.3s, ease-in-out, .1s);
            transition: 0.4s;
            // @include clip-path(polygon(0 0, 100% 0, 100% 100%, 0% 100%));
            opacity: 1;
        }
    }

    &-overlay {
        position: absolute;
        background-color: rgba($color: black, $alpha: 0.8);
        @include full;
        z-index: 1;
        @include transitionRe;
        // opacity: 0;
        // visibility: hidden;
    }

    &-main {
        @include mid;
        z-index: 2;
        width: calc(100% - 3rem);
        max-width: 95%;
        max-width: 120rem;
        opacity: 0;
        visibility: hidden;

        &-wrapper {
            border-radius: 6px;
            position: relative;
            background: white;
            border-radius: 1rem;
        }
    }

    &-over {
        overflow-y: auto;
        overflow-x: hidden;
        min-height: 40vh;
        max-height: 81vh;
        background-color: white;
        border-radius: 0.8rem;
        margin: auto;
        position: relative;
        z-index: 2;
    }

    &-wrapper {
        width: 100%;
        position: relative;
        padding: 4rem;

        @include max(1200px) {
            padding: 2rem
        }
    }

    &-title {
        @include fz-24;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 1.6rem;
    }

    &-close {
        position: absolute;
        cursor: pointer;
        border-radius: 100rem;
        background-color: white;
        z-index: 3;
        width: 3rem;
        height: 3rem;
        right: 0;
        top: 0;
        transform: translate(50%, -50%);
        transition: 0.4s;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: var(--sdow);

        .icon {
            color: var(--color-pri);
            transition: 0.4s;
            font-size: 1.2rem;
        }

        @include hover {
            background-color: var(--color-pri);

            .icon {
                color: white;
            }
        }
    }
}

.popup-bg {
    max-width: 65rem;
    .popup-wrapper {
        padding: 2.4rem 4rem;
        .form-ip textarea {
            min-height: unset;
        }
        @include max(600px) {
            padding: 2.4rem 1.6rem;
        }
    }
    .popup-over {
        max-height: 90vh;
        border-radius: 2.4rem;
        @include max(1200px) {
            border-radius: 1rem;
        }
    }
}
.popup-compare {
    &:has(.inputDetect:checked) {
        .compare-list {
            transform: translateY(0);
        }
        .compare-list-overlay {
            opacity: 1;
            pointer-events: visible;
        }
        .popup-over {
            overflow: hidden;
        }
    }
    &-wrap {
        
    }
    --gap:4rem;
    @include max(768px) {
        --gap:1.5rem;
    }
    .popup-wrapper {
        padding:var(--gap);
    }
    .popup-over {
        max-height: calc(100vh - 3rem);
    }
}