.page-home {
    overflow: hidden;
}

.homes-banner {
    &__item {
        position: relative;

        .bg {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;

            img,
            video {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
            }
        }
    }

    &__content {
        min-height: calc(971/1728*100vw);

        @include min(1201px) {
            // min-height: 100vh;
        }
    }
}

.homes-timer {
    padding: 1.6rem;

    &__block {
        background: linear-gradient(180deg, #1A1F91 0%, #0B00CF 22.24%, #F5F7FF 100%);
        border-radius: var(--radius-xl) var(--radius-xl) 0 0;
        padding: var(--pd-sc) 0;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: calc(647/1728*100vw);

        .bg {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;

            img {
                height: 100%;
                width: 100%;
                object-fit: contain;
                display: block;
            }

            &-inner {
                aspect-ratio: 728/720;
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                opacity: 0;
                mix-blend-mode: color-dodge;
                width: 40%;
                transition: 0.8s;

                &:nth-child(2) {
                    transform: translateY(-50%) scaleX(-1);
                    right: unset;
                    left: 0;
                }
            }

            &-line {
                position: absolute;
                aspect-ratio: 713.93/405.06;
                width: 100%;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
            }

            &-circle {
                position: absolute;
                aspect-ratio: 1;
                width: 100%;
                top: 50%;
                right: 0;
                transform: translateY(-50%) translateX(50%);
                opacity: 0.4;

            }
        }

        &.inview {
            .bg {
                &-inner {
                    opacity: 1;

                    &:nth-child(2) {
                        opacity: 1;
                    }
                }

                &-circle {
                    img {
                        animation: Spinner 40s linear infinite;
                    }
                }
            }
        }

    }

    &__content {
        max-width: 57.1rem;
        margin: 0 auto;
        position: relative;
        z-index: 2;

    }

    &__box {
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        .para {
            @include fz-40;
            text-transform: uppercase;
            font-weight: 700;
            color: white;
        }

        .cdw {
            flex-shrink: 0;
            width: 100%;

            &-item {
                width: calc(100% / 3);

                &:last-child {
                    display: none;
                }

                padding:5% 10%;

                @include max(500px) {
                    padding: 5% 7%;
                }
            }
        }
    }
}

.homes-about {
    position: relative;

    @keyframes hand {
        0% {
            transform: rotate(0);
        }

        50% {
            transform: rotate(3deg);
        }

        100% {
            transform: rotate(0);
        }
    }

    .particle-square {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 140%;
        height: 100%;

        canvas {
            height: 100%;
            width: 100%;
            opacity: 0.7;
        }
    }

    .container {
        max-width: var(--recontainer);
        margin-right: 0;
        padding: 0;
        padding-left: 1.5rem;

        @include max(1200px) {
            padding-left: 0;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        z-index: 2;
    }

    &__box {
        width: 55%;

        .btn-box {
            margin-top: 2.4rem;
        }
    }

    &__hand {
        width: 45%;
        transform: translateX(50%) scale(0.2);
        transform-origin: right bottom;
        transition: 0.8s;

        &-pd {
            padding-top: calc(534/626*100%);
            position: relative;
        }

        &-wrap {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            transform-origin: right bottom;

        }

        &.inview {
            transform: translateX(0);

            .homes-about__hand-wrap {
                animation: hand 10s ease-in-out infinite;
            }

            .homes-about__hands {}

            .homes-about__global {
                img {
                    animation: Spinner 20s linear infinite;
                }
            }
        }
    }

    @include max(700px) {
        &__content {
            flex-direction: column;
        }

        &__box {
            width: 100%;
            padding-right: 1.5rem;

            .head-verti {
                text-align: center;
            }

            .homes-content {
                text-align: center;

                .homes-content__btn {
                    width: fit-content;
                    margin: 0 auto;
                }
            }
        }

        &__hand {
            width: 100%;
            padding-top: 2.4rem;
        }
    }

    &__hands {
        aspect-ratio: 626/309;
        width: 100%;
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 3;

        img {
            width: 100%;
            object-fit: contain;
            display: block;
        }

        &:nth-child(2) {
            z-index: 1;
        }
    }

    &__global {
        aspect-ratio: 364/364;
        width: 56%;
        position: absolute;
        left: 7%;
        top: 0%;
        z-index: 2;

        img {
            width: 100%;
            object-fit: contain;
        }
    }
}

.homes-content {
    .mona-content {
        max-width: 86rem;

        h3 {
            @include fz-28;
            font-weight: 700;
            color: var(--color-pri);
            text-transform: uppercase;
            margin-bottom: 1rem;

            @include max(800px) {
                @include fz-18;
            }
        }

        p {
            text-align: justify;
            @include fz-20;
        }
    }
}

.homes-tag {
    padding: 1rem 1.2rem;
    @include fz-18;
    position: relative;
    width: fit-content;
    text-transform: uppercase;
    font-weight: 700;
    overflow: hidden;
    border-radius: 100rem;
    color: white;
    margin-bottom: 1rem;

    &::after,
    &::before {
        @include pseudo;
        z-index: -1;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        font-size: 1.5em;
    }

    &::after {
        background: var(--color-linear);
    }

    &::before {
        background: var(--color-linear2);
    }

    .txt {
        display: flex;
        gap: .8rem;
        white-space: nowrap;
        color: inherit;
        line-height: 1;
        align-items: center;

        &::after,
        &::before {
            content: "•";
            display: inline-block;
            color: inherit;
        }
    }

    @include max(600px) {
        padding: .5rem 1rem;

        .txt {
            font-size: 1.4rem;
            font-weight: 500;
        }
    }
}

.homes-slideV {
    padding: var(--pd-sc) 0;
    position: relative;

    .bg {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;

        img {
            width: 100%;
            height: auto;
            display: block;
            object-fit: contain;
        }

        @include max(700px) {
            top: unset;
            bottom: 0;
        }
    }

    .swiper-control {
        display: none;
    }

    &__swiper {
        overscroll-behavior: contain;

        .swiper {

            &-slide {
                .homes-slideV__item {
                    .num {
                        opacity: 0;
                    }

                    .homes-tag {
                        transform: translateX(4rem);
                        opacity: 0;
                    }

                    .title-40 {
                        .word {
                            .char {
                                transform: translateX(4rem);
                                opacity: 0;
                            }
                        }
                    }

                    .mona-content {
                        transform: translateX(4rem);
                        opacity: 0;
                    }

                    .btn-detail {
                        transform: translateX(4rem);
                        opacity: 0;
                    }

                    .img {
                        clip-path: inset(0 0 0 100%);
                    }
                }

                &-active {
                    .homes-slideV__item {
                        .num {
                            opacity: 0.2;
                        }

                        .homes-tag {
                            transform: translateX(0);
                            opacity: 1;
                            transition: 0.8s;
                        }

                        .title-40 {
                            .word {
                                .char {
                                    transform: translateX(0);
                                    opacity: 1;
                                    transition: 0.6s calc(var(--char-index) * 0.02s) ease-in-out;
                                }
                            }
                        }

                        .mona-content {
                            transform: translateX(0);
                            opacity: 1;
                            transition: 0.8s;
                            transition-delay: 0.6s;
                        }

                        .btn-detail {
                            transform: translateX(0);
                            opacity: 1;
                            transition: 0.8s;
                            transition-delay: 0.8s;
                        }

                        .img {
                            clip-path: inset(0 0 0 0);
                            transition: 0.8s;
                            transition-delay: 1s;
                        }
                    }
                }
            }
        }
    }

    &__item {
        .container {
            position: relative;
        }

        .num {
            font-size: clamp(7rem, calc(0.0328rem + 13.9344vw), 24rem);
            font-weight: 700;
            color: var(--color-pri);
            text-transform: uppercase;
            line-height: .6;
            position: absolute;
            top: 0;
            transform: translateX(-120%);
            -webkit-text-stroke: 0.2rem #111111;
            -webkit-text-fill-color: transparent;
            paint-order: stroke fill;

            &-txt {}

        }

        .flex {
            .flex-left {
                width: calc(4/12*100%);

                .wrapper {
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
                    max-width: 33.8rem;
                }
            }

            .flex-right {
                width: calc(8/12*100%);

                .wrapper {
                    .img {
                        @include imgPd(576, 864);
                        border-radius: var(--radius);

                        img {
                            @include imgCover;
                        }
                    }
                }
            }

            @include max(700px) {
                flex-direction: column;

                .flex-left {
                    width: 100%;

                    .wrapper {
                        max-width: unset;
                    }
                }

                .flex-right {
                    width: 100%;
                }
            }
        }
    }
}

.homes-partner {
    position: relative;
    z-index: 2;

    @keyframes scrollUp {
        0% {
            transform: translateY(0);
        }

        100% {
            transform: translateY(-50%);
        }
    }

    &__scroll {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        // animation-play-state: paused;
        // animation-duration: var(--speedGrid);
        animation-duration: 20s !important;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        animation-name: scrollUp;
        height: 100%;
        width: 100%;

        @include hover {
            animation-play-state: paused;
        }
    }

    &__content {
        --per: 30%;
        aspect-ratio: 1344/1076;
        overflow: hidden;
        mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, black var(--per), black calc(100% - var(--per)), rgba(0, 0, 0, 0) 100%);
    }

    &__gr {
        .homes-tag {
            width: fit-content;
            margin: 0 auto;
            margin-bottom: 2.4rem;

            .txt {
                @include fz-18;
            }
        }
    }

    &__list {
        justify-content: center;

        .col {
            width: 25%;
        }

        @include max(700px) {
            --cg: 1rem;
            --rg: 1rem;

            .col {
                width: calc(100% / 3);
            }
        }
    }

    &__item {
        .img {
            &-inner {
                @include imgPd(160, 312);
                border-radius: 1.2rem;
                border: 0.1rem solid var(--color-pri);
                transition: 0.4s;

                @include hover {
                    border-color: var(--color-second)
                }

                img {
                    @include imgContain;
                }

                @include max(700px) {
                    border-radius: .8rem;
                }
            }
        }
    }
}


.homes-schedule {
    &__btns {
        display: flex;
        gap: 2rem;
        max-width: 75.2rem;
        margin: 0 auto;

        @include max(600px) {
            gap: 1rem;
        }
    }

    &__btn {
        text-align: center;
        width: 100%;
        @include fz-18;
        font-weight: 700;
        color: var(--color-pri);
        text-transform: uppercase;
        border-radius: 2rem 2rem 0 0;
        padding: 1rem;
        padding-bottom: 3rem;
        cursor: pointer;
        transform: translateY(2rem);
        transition: 0.4s;
        overflow: hidden;
        position: relative;

        .txt {
            position: relative;
            z-index: 2;
        }

        @include max(600px) {
            .txt {
                font-size: 1.2rem;
            }
        }

        &::after,
        &::before {
            @include pseudo;
            height: 100%;
            width: 100%;
            background: #D6DBFF;
            top: 0;
            left: 0;
        }

        &::before {
            background: var(--color-linear2);
            opacity: 0;
        }

        &.active {
            transform: translateY(0);

            &::after {
                opacity: 0;
            }

            &::before {
                opacity: 1;
            }

            color: white;
        }
    }

    &__tab {
        background: linear-gradient(180deg, #1A1F91 0%, #0B00CF 100%);
        box-shadow: 4.8px -4.8px 12px rgba(0, 0, 0, 0.1);
        border-radius: var(--radius-xl) var(--radius-xl) 0 0;
        position: relative;

        .bg {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;

            img {
                width: 100%;
                height: auto;
                object-fit: contain;
                display: block;
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 2.4rem;
        position: relative;
        z-index: 2;

        @include max(900px) {
            gap: 1rem;
        }
    }

    &__item {
        .inner {
            background-color: rgba($color: #F5F7FF, $alpha: .8);
            border-radius: 1.2rem;
            padding: 2rem 4rem;

            .tt {
                text-transform: uppercase;
                @include fz-20;
                font-weight: 700;
                text-transform: uppercase;
            }

            .heads {
                margin-bottom: 1rem;
                padding-bottom: 1rem;
                border-bottom: 0.1rem solid var(--color-pri);
            }

            .bodies {
                display: flex;
                align-items: center;
                gap: 1rem;

                &:not(:last-child) {
                    margin-bottom: 2rem;

                    @include max(900px) {
                        margin-bottom: 1rem;
                    }
                }

                &-time {
                    padding: 1rem 2rem;
                    border-radius: .8rem;
                    background: var(--color-linear2);
                    color: white;
                    font-weight: 700;
                    flex-shrink: 0;
                    height: 100%;
                }

                &-avatar {
                    width: 6rem;
                    height: 6rem;
                    border-radius: 100rem;
                    border: 0.1rem solid var(--color-text);
                    flex-shrink: 0;
                    margin-left: auto;

                    img {
                        height: 100%;
                        width: 100%;
                        display: block;
                        object-fit: cover;
                    }
                }
            }

            @include max(900px) {
                padding: 1rem 2rem;
            }

            @include max(768px) {
                .tt {
                    @include fz-18;
                }

                .heads {
                    margin-bottom: 0.5rem;
                    padding-bottom: 0.5rem;
                }

                .bodies {
                    &-content {
                        .tt {
                            font-size: 1.4rem;
                        }

                        .des {
                            font-size: 1.4rem;
                        }
                    }

                    &-avatar {
                        width: 4rem;
                        height: 4rem;
                    }
                }
            }

            @include max(600px) {
                padding: .5rem 1rem;

                .bodies {
                    flex-wrap: wrap;

                    &-time {
                        padding: .5rem 1rem;
                        font-size: 1.4rem;
                    }

                    &-content {
                        width: calc(100% - 1rem - 4rem);
                    }
                }
            }

            &:not(:has(.heads)) {
                .bodies {
                    flex-wrap: nowrap;
                }
            }
        }
    }

    &__box {
        max-width: 108.6rem;
        margin: 0 auto;
    }
}

.homes-speaker {
    background: linear-gradient(180deg, #F5F7FF 0%, #F5F7FF 25%, #D6DBFF 50%, #F5F7FF 100%);

    &__btns {
        display: flex;
        max-width: 100%;
        width: fit-content;
        margin: 0 auto;
        gap: 2.4rem;
        margin-bottom: 2.4rem;
        position: relative;
        z-index: 2;

        @include max(600px) {
            gap: .8rem;
        }
    }

    &__btn {
        cursor: pointer;

        &.homes-tag {
            border: 0.1rem solid var(--color-pri);

            &::after,
            &::before {
                opacity: 0;
            }

            .txt {
                color: var(--color-pri);
            }

            &.active {
                border-color: transparent;


                &::before {
                    opacity: 1;
                }

                .txt {
                    color: white;
                }
            }
        }
    }

    &__panels {}

    &__item {
        .inner {
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .img {
                &-inner {
                    @include imgPd(361, 312);
                    border: 0.1rem solid var(--color-second);
                    border-radius: var(--radius);

                    img {
                        @include imgCover;
                        display: block;
                    }
                }
            }

            .info {
                &-name {
                    @include fz-24;
                    font-weight: 700;
                    text-align: center;
                    color: var(--color-second);
