}

.homes-three {
    &__slide {
        .swiper {
            padding-top: 2rem;

            &-slide {
                overflow: visible;

                @include max(500px) {
                    width: 100%;
                }
            }
        }
    }

    &__item {
        height: 100%;

        .inner {
            height: 100%;
            border: 0.1rem solid var(--color-pri);
            border-radius: var(--radius);
            background: #D6DBFF;
            position: relative;
            display: flex;
            flex-direction: column;

            &-head {
                min-height: 12rem;
                padding: 2rem 4rem;
                padding-bottom: 1rem;
                border-bottom: 0.1rem solid rgba($color: #0B00CF, $alpha: .1);
                @include max(1200px) {
                    min-height: 10rem;
                }
                .tt {
                    @include fz-28;
                    font-weight: 600;
                    color: var(--color-pri);
                    padding-right: 20%;
                }

                .img {
                    aspect-ratio: 142/105;
                    width: 30%;
                    position: absolute;
                    right: 0;
                    top: 0;
                    transform: translateY(-2rem);
                }
            }

            &-body {
                padding: 2rem 4rem;
                padding-top: 1rem;
                display: flex;
                flex-direction: column;
                gap: .5rem;

                .name {
                    @include fz-20;
                    font-weight: 700;
                    text-transform: uppercase;
                    color: var(--color-second);
                }

                .role {
                    color: var(--color-des2);
                }

                .iwt {
                    --iz: 1.4rem;
                    --gap: 0.8rem;
                    --fz: 1.8rem;
                    --cl: var(--color-pri);
                }
            }

            @include max(1000px) {
                .inner-head {
                    padding: 1rem;
                }

                .inner-body {
                    padding: 1rem;
                }
            }
        }
    }
}

main:has(.homes-move__map) {
    margin-bottom: -3vw;
}

.homes-move__map {
    padding: 1.6rem;
    padding-bottom: 0;

    .map {
        position: relative;

        @include min(1201px) {
            &::after {
                @include pseudo;
                height: 100%;
                width: 100%;
                top: 0;
                left: 0;
                z-index: 3;
            }

            &:active {
                &::after {
                    display: none;
                }
            }
        }

        &-inner {
            @include imgPd(564, 1696);
            border-radius: 2.4rem 2.4rem 0 0;

            @include max(600px) {
                min-height: 60vw;
            }

            iframe {
                @include imgCover;
            }
        }
    }
}

.homes-count {
    padding: 1.6rem;
    position: relative;
    z-index: 2;

    &__box {
        overflow: hidden;
        border-radius: var(--radius);
        position: relative;

        .bg {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &__content {
        position: relative;
        z-index: 2;
        padding: 12rem 0;
        color: white;

        @include max(900px) {
            padding: var(--pd-sc) 0;
        }

        .flex {
            &-left {
                .wrapper {
                    width: 50%;
                    max-width: 56.4rem;
                }
            }

            &-right {
                .wrapper {
                    width: 50%;
                    margin-left: auto;
                }
            }

            @include max(900px) {
                &-left {
                    .wrapper {
                        width: 100%;
                    }
                }

                &-right {
                    .wrapper {
                        width: 100%;
                    }
                }
            }
        }
    }

    &__numbers {
        justify-content: space-between;
    }

    &__number {
        width: fit-content;

        .inner {
            font-size: clamp(7rem, calc(3.3115rem + 7.377vw), 16rem);

            .num {
                font-size: 1em;
                line-height: 1;

                &-txt {
                    font-weight: 700;
                    font-style: italic;
                    color: white;
                }

                &-sym {
                    font-size: 0.6em;
                    color: var(--color-second);
                    font-weight: 700;
                    vertical-align: super;
                }
            }

            .txt {
                color: #D6DBFF;
                font-weight: 700;
                font-size: 0.15em;
            }
        }

        @include max(660px) {
            width: 50%;
        }
    }

}

.homes-thank {
    position: relative;

    .bg {
        position: absolute;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
        left: 0;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__content {
        min-height: calc(752/1731*100vw);
        position: relative;
        z-index: 2;
        padding: var(--pd-sc) 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 50%;
        margin-left: auto;

        .mona-content {
            p {
                text-align: justify;
            }
        }

        @include max(900px) {
            width: 100%;
            padding: var(--pd-sc) 1.5rem;
            background-color: rgba($color: #D6DBFF, $alpha: .5);
            border-radius: var(--radius);
            margin-bottom: var(--pd-sc);
        }
    }
}

.homes-document {
    position: relative;
    z-index: 2;

    &__box {
        padding: 4rem;
        border-radius: var(--radius-xl);
        border-top: 0.4rem solid var(--color-pri);
        box-shadow: 11.4286px 11.4286px 45.7143px rgba(0, 0, 0, 0.16);
        background: white;

        .heads {
            padding-bottom: 2rem;
            margin-bottom: 2rem;
            border-bottom: 0.1rem solid var(--cl-bd);
        }

        @include max(800px) {
            padding: 2rem;
        }

    }

    &__item {
        padding: 1rem 0;

        &:not(:last-child) {
            border-bottom: 0.1rem solid var(--cl-bd);
        }

        .inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: .5rem 2rem;

            .iwt {
                --iz: 4rem;
                --gap: 1rem;
                --fz: 2rem;
                font-weight: 700;

                @include max(800px) {
                    --iz: 3rem;
                    --gap: .8rem;
                    --fz: 1.4rem;
                }
            }
        }
    }
}

.page-home-after {
    .homes-news {
        position: relative;

        &::after {
            @include pseudo;
            aspect-ratio: 1728/1839;
            width: 100%;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background-image: url('../assets/images/homes-document-bg.png');
            @include default-background;
            z-index: -1;
        }
    }
}