:root {
    // item
    --text: var(--color-text);
    --text-hover: white;
    --fw: 700;
    // background sub
    --back: white;
    // sub
    --textsub: var(--color-text);
    // font size
    --fz: 1.6rem;
    --fz-arrow: 0.6em;
    // gap
    --gap: 3.2rem;
    @include max(1300px) {
        --fz: 1.4rem;
    }
}


.hd-nav {
    height: 100%;

    .menu {
        &-nav {
            height: 100%;

            >.menu-list {
                height: 100%;
                gap: var(--gap);

                >.menu-item {
                    display: flex;
                    align-items: center;
                    @include font-second;

                    &:last-child {
                        >.menu-list {
                            left: initial;
                            right: 0;
                        }
                    }

                    >.menu-link {
                        position: relative;
                        z-index: 2;
                        &::before {
                            content: "";
                            width: 100%;
                            height: 0.2rem;
                            transition: 0.4s;
                            position: absolute;
                            left: 0;
                            bottom: 0;
                            z-index: -1;
                            opacity: 0;
                            background: var(--color-pri);
                        }
                    }

                    &:hover:not(.dropdown),
                    &:has(.current-menu-item) {
                        >a {
                            &::before {
                                opacity: 1;
                            }
                            // color: var(--color-pri);
                        }
                    }

                    &.dropdown {
                        &:hover {
                            >a {
                                color: var(--color-pri);
                                &::before {
                                    opacity: 1;
                                }
                            }
                        }
                    }

                    &:hover {
                        >a {
                            color: var(--color-pri);
                            // background-color: var(--color-pri);

                            i {
                                transform: rotate(180deg);
                            }
                        }
                    }

                    >.menu-link {
                        display: flex;
                        align-items: center;
                        color: var(--text);
                    }

                    &.current_page_item,
                    &.current-menu-item,
                    &.current_page_parent,
                    &.current-menu-parent,
                    &.current-post-parent {

                        // background-color: var(--color-pri);
                        >a {
                            color: var(--text-hover);
                            font-weight: 400;
                            &::before {
                                opacity: 1;
                            }
                        }
                        @include hover {
                            >a {
                                color: white !important;
                            }
                        }

                        
                    }

                    >.menu-list {
                        top: calc(100% + 0.7rem);

                        &::after {
                            @include pseudo;
                            bottom: 100%;
                            left: 0;
                            height: 0.7rem;
                            width: 100%;
                        }
                    }
                }
            }
        }

        &-list {
            display: flex;
            align-items: center;
        }

        &-item {
            position: relative;
            transition: 0.2s;

            &:hover {
                >.menu-list {
                    opacity: 1;
                    visibility: visible;
                    pointer-events: visible;
                    transform: translate(0, 0);

                    >.menu-item {

                        &.current_page_item,
                        &.current-menu-item {
                            background-color: var(--color-pri);

                            >a {
                                &::before {
                                    width: 100%;
                                }

                                color: var(--text-hover);
                                color: white;
                            }
                        }
                    }
                }

                >.megas {
                    transform: translateY(0) translateX(-50%);
                    opacity: 1;
                    visibility: visible;
                    pointer-events: visible;
                }
            }

            .menu-link,
            a {
                transition: 0.2s;

                i {
                    transition: 0.2s;
                }
            }

            >.menu-link,
            >a {
                color: var(--textsub);
                font-weight: var(--fw);
                display: block;
                font-size: var(--fz);

                >i {
                    display: none;
                    margin-left: 0.4rem;
                    font-size: var(--fz-arrow);
                    color: inherit;
                    margin-bottom: -0.1em;
                }
            }

            &.dropdown {
                >a {
                    >i {
                        display: inline-block;
                    }
                }
            }

            // Sub
            >.menu-list {
                position: absolute;
                top: 0;
                left: 0rem;
                min-width: 20rem;
                // max-width: 30rem;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                background: var(--back);
                box-shadow: var(--sdow);
                border-radius: .8rem;
                transition: 0.4s;
                @include hidden(0, -5px);
                padding: .5rem;
                gap: .5rem;

                &::before {
                    @include pseudo;
                    z-index: -1;
                    height: 100%;
                    width: 100%;
                    left: 0;
                    top: 0;
                    border-radius: .8rem;
                    transition: 0.4s;
                }

                .menu-item {
                    &:not(:first-child) {
                        // border-top: 0.1rem solid var(--cl-bd);
                    }

                    color: var(--text);

                    .menu-link {
                        i {
                            &::before {
                                content: "\f078";
                            }
                        }
                    }

                    @include hover {
                        >a {
                            // background-color: var(--color-pri);
                        }

                        i {
                            color: white;
                        }
                    }

                    &.current_page_item,
                    &.current-menu-item {
                        >a {
                            &::before {
                                width: 100%;
                            }

                            color: var(--color-pri);
                        }
                    }
                }

                // sub
                >.menu-item {
                    margin-left: 0;
                    width: 100%;

                    // white-space: nowrap;
                    border-radius: 0.8rem;

                    &:hover {
                        background-color: var(--color-pri);

                        &::after {
                            opacity: 1;
                        }

                        >.menu-link,
                        >a {
                            // color: var(--text-hover);
                            color: white;

                            &::before {
                                width: 100%;
                            }

                            >i {
                                color: var(--text-hover);
                                text-decoration: none;
                                color: white;
                            }
                        }
                    }

                    >.menu-link,
                    >a {
                        // padding: .5rem 1rem;
                        padding: .5rem 1rem;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        position: relative;
                        padding-left: 1.2rem !important;
                        color: var(--text);
                        font-weight: 400;

                        >i {
                            transform: rotate(-90deg) translate(5px, 0);
                            margin-top: 0.8rem;
                            font-size: 0.8em;
                        }
                    }

                    >.menu-list {
                        left: calc(100% + 1rem);

                        &::after {
                            @include pseudo;
                            right: 100%;
                            top: 0;
                            width: 1rem;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
}

.mobile-nav {
    --text-hover: var(--color-pri);

    .menu {
        &-nav {
            >.menu-list {
                >.menu-item {
                    // add
                    padding: 1rem;
                    position: relative;
                    // text-transform: uppercase;

                    &.current_page_item,
                    &.current-menu-item,
                    &.current_page_parent,
                    &.current-menu-parent,
                    &.current-post-parent {
                        >a {
                            color: var(--text-hover);

                            &::after {
                                opacity: 1;
                                transform: translateY(0) !important;
                            }
                        }
                    }

                    &:has(.current-menu-item) {
                        >a {
                            color: var(--text-hover);
                        }
                    }

                    &.active {
                        >a {
                            color: var(--text-hover);

                            &::after {
                                width: 3rem;
                                background-color: var(--text-hover);
                            }
                        }
                    }

                    >a {
                        padding: 1.2rem;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        color: #303030;
                        font-size: 1.6rem;
                        font-weight: 600;
                        border-bottom: 1px solid var(--cl-bd);
                        position: relative;

                        // @include font-second;
                        &::after {
                            @include pseudo;
                            left: 1rem;
                            bottom: 0;
                            width: 2rem;
                            height: 0.4rem;
                            transition: 0.4s;
                            width: 0;
                            display: none;
                        }

                        &::before {
                            display: none;
                        }

                        // add
                        background-color: rgb(255, 237, 235);
                        padding: 0.8rem 1rem;
                        padding-right: 0 !important;
                    }
                }
            }
        }

        &-item {
            &.dropdown {
                >a {
                    >i {
                        display: flex;
                    }
                }
            }

            &.active {
                >a {
                    color: var(--color-pri);

                    >i {
                        transform: rotate(180deg);
                    }
                }
            }

            a {
                position: relative;

                i {
                    width: 2rem;
                    height: 2rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    display: none;
                    transition: 0.4s;
                    font-size: var(--fz-arrow);
                }
            }

            >a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 1.2rem;
                font-size: 1.6rem;
                color: var(--color-text);
                font-weight: 700;
                padding-right: 0;


                @include hover {
                    color: var(--text-hover);
                }
            }

            >.menu-list {
                display: none;
                margin-left: 1.6rem;
                position: relative;

                .menu-item {
                    .menu-link {
                        &:first-child {
                            padding-top: 0;
                        }
                    }
                }

                &::before {
                    content: '';
                    position: absolute;
                    top: 2rem;
                    bottom: 1rem;
                    left: 0;
                    width: 1px;
                }

                // modifier
                margin-left: 0;

                &::before {
                    content: none;
                }

                >.menu-item {

                    &.current_page_item,
                    &.current-menu-item {
                        >a {
                            color: var(--text-hover);
                        }
                    }

                    &:has(.current-menu-item) {
                        >a {
                            color: var(--text-hover);
                        }
                    }

                    >.menu-link {
                        // font-style: italic;
                        font-weight: 500;
                        font-size: 1.4rem;
                    }

                    &:not(:first-child) {
                        >.menu-link {
                            padding-top: 0;
                        }
                    }

                    &:last-child {
                        >.menu-link {
                            padding-bottom: 0;
                        }
                    }

                    >.menu-list {
                        padding-left: 2rem;
                        position: relative;

                        &::after {
                            @include pseudo;
                            top: 1rem;
                            height: calc(100% - 1rem);
                            left: 2rem;
                            width: 0.1rem;
                            background-color: var(--cl-bd);
                        }
                    }
                }
            }
        }
    }
}

@keyframes menuLa {
    0% {
        clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }

    50% {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }

    75% {
        transform: rotate(10deg);
    }

    100% {
        transform: rotate(0deg);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
}

.megas {
    @include max(1200px) {
        display: none;
    }

    position: fixed;
    width: 126.3rem;
    left: 50%;
    transform: translateX(-50%);
    top: calc(var(--size-hd));
    background-color: white;
    padding: 3.2rem;
    border-radius: var(--rs);
    border: 0.1rem solid var(--color-pri);
    transition: 0.4s;
    // 
    @include hidden(-50%, 0);

    // 
    &-list {
        flex-wrap: nowrap;
    }

    &-item {
        width: 25%;

        &:nth-child(3) {
            min-width: 40rem;
            flex-shrink: 0;
        }

        &:nth-child(4) {
            .menu-list {
                align-items: unset;
            }

            .menu-item {
                width: 100%;
            }
        }

        .tt {
            font-weight: 600;
            margin-bottom: 1.2rem;
            padding-bottom: 0.8rem;
            border-bottom: 0.1rem solid #E8E8E8;
        }

        .menu-list {
            display: flex;
            flex-wrap: wrap;
            --rg: 1.2rem;
            --cg: 0.8rem
        }

        .menu-item {
            width: 50%;
            @include lc(1);
        }

        .menu-link {
            text-transform: capitalize;
            font-size: 1.4rem;
            color: #595959;
            transition: 0.4s;
            font-weight: 300;

            @include hover {
                color: var(--text-hover);
            }
        }
    }
}