// count
.count {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    width: fit-content;
    gap: 1rem;

    &-input {
        height: 4rem;
        width: 6.4rem;
        text-align: center;
        border-radius: 0.6rem;
        padding: 1rem;
        border: 0.1rem solid #CCD3D8;
        flex-shrink: 0;
        display: block;
    }

    &-number {
        display: none;
    }

    &-btn {
        width: 3.2rem;
        height: 3.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border-radius: 100rem;
        border: 0.1rem solid #CCD3D8;
        transition: 0.4s;
        background-color: #FFFFFF;

        i {
            font-size: 1rem;
        }

        &:hover {
