}

// checker
.checker {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 1rem;
    user-select: none;
    width: fit-content;

    .sqr {
        width: 1.6rem;
        height: 1.6rem;
        flex-shrink: 0;
        position: relative;
        display: block;
        margin-bottom: -0.2rem;

        &::after {
            @include pseudo;
            width: 100%;
            height: 100%;
            background-image: url("../assets/images/check-empty.svg");
            @include default-background;
            left: 0;
            top: 0;
        }
    }

    .circle {
        width: 1.6rem;
        height: 1.6rem;
        flex-shrink: 0;
        position: relative;
        display: block;

        &::after {
            @include pseudo;
            width: 100%;
            height: 100%;
            background-image: url("../assets/images/radio-empty.svg");
            @include default-background;
            left: 0;
            top: 0;
        }
    }

    .txt {
        display: block;
        font-size: 1.4rem;

        @include max(500px) {
            font-size: 1.4rem;
        }

        a {
            text-decoration: underline;
            color: var(--color-second);
        }
    }

    .num {
        margin-left: auto;
    }

    input {
        display: none;
    }

    input:checked~.sqr {
        &::after {
            background-image: url("../assets/images/check-fill.svg");
        }

        +.txt {
            // color: var(--color-second);
        }
    }

    input:checked~.circle {
        &::after {
            background-image: url("../assets/images/radio-fill.svg");
        }

        +.txt {
            // color: var(--color-second);
        }
    }
}

// 
.drops {
    position: relative;
    width: 100%;
    height: 100%;
    // display: none;

    .drop {
        position: absolute;
        background: transparent;
        box-shadow:
            inset 4px 4px 6px rgba(0, 0, 0, 0.05),
            6px 10px 6px rgba(0, 0, 0, 0.1),
            8px 10px 10px rgba(0, 0, 0, 0.05),
            inset -6px -6px 8px rgba(255, 255, 255, 0.9);
        animation: fallDown linear infinite, wobbleRadius 4s ease-in-out infinite;
        mix-blend-mode: overlay;
        will-change: transform;
        // display: none;
    }

    .drop::before,
    .drop::after {
        content: '';
        position: absolute;
        background: #fff;
        border-radius: 50%;
    }

    .drop::before {
        top: 20%;
        left: 25%;
        width: 20%;
        height: 20%;
    }

    .drop::after {
        top: 30%;
        left: 50%;
        width: 12%;
        height: 12%;
    }

    @keyframes fallDown {
        0% {
            transform: translateY(-20vh);
        }

        100% {
            transform: translateY(calc(700/1512*100vw)) scaleX(0.5);
        }
    }

    @keyframes wobbleRadius {
        0% {
            border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
        }

        50% {
            border-radius: 60% 40% 55% 45% / 50% 60% 40% 50%;
        }

        100% {
            border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
        }
    }
}

