// 


.circle-cta {
    --bg: var(--color-pri);
    --text: var(--color-text);
    position: relative;
    border: 0;
    background: transparent;
    padding: 0;
    cursor: pointer;
    display: grid;
    place-items: center;
    border-radius: 50%;
    outline: none;

    /* vòng chữ */
    svg.text-ring {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        transform-origin: 50% 50%;
        animation: spin 12s linear infinite;
        pointer-events: none;
    }

    &:hover svg.text-ring {
        animation-play-state: paused;
    }

    @keyframes spin {
        to {
            transform: rotate(360deg);
        }
    }

    text {
        font-weight: 600;
        font-size: var(--font-size);
        fill: var(--text);
        letter-spacing: 0.5px;
    }

    /* tròn đỏ giữa */
    .dot {
        border-radius: 50%;
        display: grid;
        place-items: center;
        box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15) inset,
            0 6px 24px rgba(0, 0, 0, 0.08);
        transition: transform 0.25s ease;
        background: var(--bg);
    }

    &:hover .dot {
        transform: scale(1.04);
    }

    &:active .dot {
        transform: scale(0.98);
    }

    /* mũi tên */
    .arrow {
        width: 34px;
        height: 34px;

        img {
            height: 100%;
            width: 100%;
            display: block;
        }
    }

    &.white {
        --text: white;
    }

}

.homes-news__slide {
    .progressbar {
        position: relative;
        width: 100%;
        height: 0.2rem;
        margin-top: 2.4rem;

        .progress-track {
            width: 100%;
            height: 100%;
            background: rgba(255, 255, 255, 0.25);
            border-radius: 100rem;
        }

        .progress-fill {
            --progress: 0%;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: var(--progress);
            background: #fff;
            border-radius: 100rem;
            transition: width 0.3s ease;

            &::after {
                @include pseudo;
                right: 0;
                top: 50%;
                transform: translateY(-50%) rotate(45deg);
                width: .5rem;
                height: .5rem;
                background-color: #fff;

            }
        }
    }


}

